<template>
  <div class="test-pages pa-3">
    <p>Channel Youtube : {{ youtubeTitle }}</p>
    <exampleComponent name="Syarifuddin Sulthon Al Nashir" occupation-name="Front End Developer">
      <!-- <div slot="custom-card">Uhuy</div> -->
    </exampleComponent>
    <v-text-field
      v-model="email"
      outlined
      background-color="white"
      placeholder="Masukkan Email Kamu"
      dense
      fixed
      type="email"
      hide-details
      class="my-2"
    ></v-text-field>
    <div class="color-red avenir-12-reg" v-if="validation.email">{{ validation.email }}</div>

    <!-- <v-layout row justify-center>
      <v-flex sm12 md2 v-for="i in 7" :key="i">
        ceki
      </v-flex>
    </v-layout> -->
  </div>
</template>
<script>
const exampleComponent = () => import('@/components/example.vue')
export default {
  components: {
    exampleComponent
  },
  data: function () {
    return {
      email: '',
      validation: {
        email: ''
      },
      youtubeTitle: ''
    }
  },
  watch: {
    email (value) {
      if (value === '') {
        this.validation.email = ''
      } else {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        if (pattern.test(value) === false) {
          this.validation.email = 'Format e-mail salah'
        } else {
          this.validation.email = ''
        }
      }
    }
  },
  computed: {
    youtubeData () {
      return this.$store.getters['global/state'].youtube
    }
  },
  methods: {
    async getDataYoutubeOverview () {
      // this.$loading('start')
      try {
        const payload = {
          part: 'snippet,statistics',
          id: 'UCkXmLjEr95LVtGuIm3l2dPg',
          key: 'AIzaSyC2gyZypDCTVQVH2np2_z5vdWjQwVCiadQ'
        }
        const res = await this.$store.dispatch('global/getOverview', payload)
        this.youtubeTitle = res.items[0].snippet.title
      } catch (err) {
        throw new Error(err)
      } finally {
        // this.$loading('finish')
      }
    }
  },
  created () {
    this.getDataYoutubeOverview()
  }
}
</script>
<style lang="sass">
.test-pages
  .color-red
    color: red

</style>
